<template>
    <div class="post-view">
        <div class="container quillWrapper">
            <h2> {{ this.blogTitle }}</h2>
            <img :src="this.blogCoverPhoto" alt="">
            <div class="post-content ql-editor" v-html="blogHTML"></div>
        </div>
    </div>
</template>

<script>
export default {
    name:'postPreview',
    computed:{
        blogTitle(){
            return this.$store.state.blogTitle;
        },
        blogHTML(){
            return this.$store.state.blogHTML
        },
        blogCoverPhoto(){
            return this.$store.state.blogPhotoFileURL
        },
    },
}
</script>

<style lang="scss">
.post-view {
    min-height: 100%;

    .container {
        max-width: 1000px;
        padding: 60px 25px;
    }

    .ql-editor {
        padding: 0px;
    }

    h2 {
        margin-bottom: 16px;
        font-size: 32px;
        font-weight: 300;
    }

    img {
        width: 100%;
        margin-bottom: 32px;
    }
}
</style>